import React, { Component } from 'react';
import 'normalize.css';
import './reset.css';
import './App.css';
import UserDialog from './UserDialog'
import TodoTnput from './TodoInput'
import TodoItem from './TodoItem'
import {getCurrentUser, signOut, TodoModel, signIn} from './leanCloud'

class App extends Component {
      constructor(props){
          super(props)
          this.state = {
            user: getCurrentUser() || {},
            newTodo: '',
            todoList: []
        }

        {/*实际上获取的 Todo 是「所有用户」创建的 Todo，而不是当前用户创建的 Todo。不行的话，开一个隐私窗口，用另一个用户名来创建 Todo，看是不是会出 bug*/}
        let user = getCurrentUser()
        if(user){
          TodoModel.getByUser(user, (todos) =>{
            let stateCopy = JSON.parse(JSON.stringify(this.state))
            stateCopy.todoList = todos
            this.setState(stateCopy)
          })
          }
      }

      render() {
        {/*过滤，让没有删除的显现出来*/}
        let todos = this.state.todoList
        .filter((item)=>{
                        console.log(item.deleted)
                        return !item.deleted}
        )
        .map((item,index)=>{
            return (
              <li key={index}>
                  <TodoItem todo={item}
                   onToggle={this.toggle.bind(this)}
                   onDelete={this.delete.bind(this)}/>
              </li>
            )
        });

        return (
          <div className="app">
              <h2>{this.state.user.username || '我'}的待办
              {this.state.user.id ? <button className="quit" onClick={this.signOut.bind(this)}>退出</button>:null}
              </h2>
              <div className="inputWrapper">
                  <TodoTnput
                   content={this.state.newTodo}
                   changeContent={this.changeText.bind(this)}
                   onSubmit={this.addTodo.bind(this)} />
              </div>
              <ol className="todoList">
                  {todos}
              </ol>
              {this.state.user.id ?
                null :
                <UserDialog
                 onSignUp={this.onSignUpOrSignIn.bind(this)}
                 onSignIn={this.onSignUpOrSignIn.bind(this)}/>}
          </div>
        )
      }

      signOut(){
        let stateCopy = JSON.parse(JSON.stringify(this.state))
        stateCopy.user = {}
        this.setState(stateCopy)
      }

      onSignUpOrSignIn(user){
        let stateCopy = JSON.parse(JSON.stringify(this.state))
        stateCopy.user = user
        this.setState(stateCopy)
      }

      componentDidUpdate(){

      }

      /*删除一条信息*/
      delete(event, todo){
        TodoModel.destroy(todo.id, () => {
          todo.deleted = true
          this.setState(this.state)
        })
      }

      toggle(e, todo){
        let oldStatus = todo.status
        todo.status = todo.status === 'completed' ? '' : 'completed'
        TodoModel.update(todo, () => {
          this.setState(this.state)
        }, (error) => {
          todo.status = oldStatus
          this.setState(this.state)
        })
      }

      changeText(event){
        this.setState({
          newTodo: event.target.value
        });
      }

      /*增加一条todo,回车的时候触发*/
      addTodo(event){
        let newTodo = {
          title: event.target.value,
          status: null,
          deleted: false
        }

        TodoModel.create(newTodo, (id) => {
          newTodo.id = id
          this.state.todoList.push(newTodo)
          this.setState({
            newTodo: '',
            todoList: this.state.todoList
          })
          }, (error) => {
          console.log(error)
      })
      }
}

export default App




